.skillscntr{
    width: 100%;
    height: 100%;
    padding: 20px 10px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 35px;
    overflow: scroll;
}
.skillscntr::-webkit-scrollbar{
    display: none;
}
:root{
    --color-text: #616161;
    --color-text-btn: #ffffff;
    --card1-gradient-color1: #dc4d25;
    --card1-gradient-color2: #e86229;

    --card2-gradient-color1: #214ce5;
    --card2-gradient-color2: #214ce5;

    --card3-gradient-color1: #d3b32c;
    --card3-gradient-color2: #f7d138;

    --card4-gradient-color1: #5ed3f3;
    --card4-gradient-color2: #212121;

    --card5-gradient-color1: #7377ad;
    --card5-gradient-color2: #000000;

    --card6-gradient-color1: #f7bd2f;
    --card6-gradient-color2: #34668f;
    
    --card7-gradient-color1: #207196;
    --card7-gradient-color2: #f7f7f7;
}
.card-wrap{
    width: 220px;
    background: #fff;
    border-radius: 20px;
    border: 5px solid #fff;
    overflow: hidden;
    color: var(--color-text);
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px,
                rgba(0, 0, 0, 0.23) 0px 6px 6px;
    cursor: pointer;
    transition: all .2s ease-in-out;
}  
.card-wrap:hover{
    transform: scale(1.1);
}
.card-header{
    height: 200px;
    width: 100%;
    background: red;
    border-radius:100% 0% 100% 0% / 0% 50% 50% 100%;
    display: grid;
    place-items: center;
  
  }
  .card-header svg{
    color: #fff;
    font-size: 72px;
    width: 70px;
    height: 70px;
  }  
  .card-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin: 0 auto;
  }
  .card-title{
    text-align: center;
    text-transform: uppercase;
    font-size: 16px;
    margin-top: 10px;
    margin-bottom: 20px;
    
  }
  .card-text{
    text-align: center;
    font-size: 12px;
    margin-bottom: 20px;
  }
  .card-btn{
    border: none;
    border-radius: 100px;
    padding: 5px 30px;
    color: #fff;
    margin-bottom: 15px;
    text-transform: uppercase;
  }

  .card-header.one{
    background: linear-gradient(to bottom left, var(--card1-gradient-color1), var(--card1-gradient-color2));
  }  
  .card-header.two{
    background: linear-gradient(to bottom left, var(--card2-gradient-color1), var(--card2-gradient-color2));
  }
  .card-header.three{
    background: linear-gradient(to bottom left, var(--card3-gradient-color1), var(--card3-gradient-color2));
  }
  .card-header.four{
    background: linear-gradient(to bottom left, var(--card4-gradient-color1), var(--card4-gradient-color2));
  } 
  .card-header.five{
    background: linear-gradient(to bottom left, var(--card5-gradient-color1), var(--card5-gradient-color2));
  }
  .card-header.six{
    background: linear-gradient(to bottom left, var(--card6-gradient-color1), var(--card6-gradient-color2));
  }
  .card-header.seven{
    background: linear-gradient(to bottom left, var(--card7-gradient-color1), var(--card7-gradient-color2));
  } 




  .card-btn.one{
    background: linear-gradient(to left, var(--card1-gradient-color1), var(--card1-gradient-color2));
  } 
  .card-btn.two{
    background: linear-gradient(to left, var(--card2-gradient-color1), var(--card2-gradient-color2));
  }
  .card-btn.three{
    background: linear-gradient(to left, var(--card3-gradient-color1), var(--card3-gradient-color2));
  }
  .card-btn.four{
    background: linear-gradient(to left, var(--card4-gradient-color1), var(--card4-gradient-color2));
  }  
  .card-btn.five{
    background: linear-gradient(to left, var(--card5-gradient-color1), var(--card5-gradient-color2));
  }  
  .card-btn.six{
    background: linear-gradient(to left, var(--card6-gradient-color1), var(--card6-gradient-color2));
  }  
  .card-btn.seven{
    background: linear-gradient(to left, var(--card7-gradient-color1), var(--card7-gradient-color2));
  }  
