.content{
    flex: 1;
}
/* --------------About----------- */
.aboutcntr{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 10px 5px;
    overflow: hidden;
}
.profileimg{
    display: flex;
    justify-content: center;
    padding: 10px 5px;
    overflow: hidden;
}
.profileimg img{
    width: 150px;
    height: 200px;
}
.profiledetails{
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 5px;
}
.profiledetails h1{
    font-family: roboto;
}
#mydetails{
    padding: 10px 50px;
    text-align: justify;

}
.profiledetails button{
    padding: 20px 30px;
    border-radius: 15px;
    border: none;
    color: #ffffff;
    background-color: orange;
    cursor: pointer;
}