.sidenav{
    width: 15%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    justify-content: center;
    padding: 10px 0px;
    overflow: scroll;
}
.sidenav::-webkit-scrollbar{
    display: none;
}
.nav-link{
    width: 100%;
    display: flex;
    justify-content: center;
    text-decoration: none;
}
.sidecntp{
    width: 90%;
    padding: 8px 5px;
    border-radius: 12px;
    overflow: hidden;
    display: flex;
    cursor: pointer;
    gap: 10px;
    font-size: 20px;
    transition: 0.3s linear;
}
.sidecntp:hover{
    background-color: rgb(104, 104, 104) !important;
}
.sideicon{
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 5px;
}
.sidename{
    flex: 1;
    display: flex;
    align-items: center;
    color : #ffffff;
}