.workcntr{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    gap: 10px;
    padding: 10px;
    flex-direction: column;
    overflow: scroll;
}
.workcntr::-webkit-scrollbar{
    display: none;
}
.works{
    width: 100%;
    height: 200px;
    background-color: rgb(219, 219, 219);
    border-radius: 15px;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    transition: 0.3s linear;
}
.works:hover{
    background-color: rgb(187, 185, 185);
}
.wimgcntr{
    background-color: yellow;
    width: 180px;
    height: 180px;
    border-radius: 15px;
    cursor: pointer;
    overflow: hidden;
}
.wimgcntr img{
    width: 100%;
    height: 100%;
}
.wdetails{
    flex: 1;
    background-color: rgb(206, 206, 252);
    height: 185px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    gap: 8px;
    overflow: scroll;
    cursor: pointer;
}
.wdetails::-webkit-scrollbar{
    display: none;
}
.wdetails p{
    padding: 0px 10px;
}
.wdetails button{
    padding: 8px 10px;
    border-radius: 12px;
    cursor: pointer;
    border: none;
    background-color: orange;
}