.profilecntr{
    width: 25%;
    background-color: rgb(219, 219, 219);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 20px 5px;
    box-shadow: 0px 0px 7px 1px rgb(3, 88, 247);
}
.imgcntr{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
}
.profilecntr .imgcntr img{
    width: 100px;
    height: 150px;
    border-radius: 20px;
}
.linkdin{
    text-align: center;
}
.last-box{
    width: 100%;
    height: 300px;
    padding: 0px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.education{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    padding: 10px 10px;
    border-radius: 12px;
    background-color: rgb(206, 218, 255);
    transition: 0.3s linear;
    cursor: pointer;
}
.education:hover{
    box-shadow: 0px 0px 3px 1px rgb(53, 103, 255);
}
.education p{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.education p svg{
    height: 10px;
    width: 10px;
}
.skills{
    width: 100%;
    text-align: center;
    border-radius: 12px;
    background-color: rgb(213, 250, 250);
    padding: 10px;
    transition: 0.3s linear;
    cursor: pointer;
}
.skills:hover{
    box-shadow: 0px 0px 3px 2px rgb(54, 253, 253);
}
.skills .the-skills{
    padding: 10px 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
}