.contact{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.contactcntr{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
}
.inputfieldcntr h1{
    text-align: center;
    color: rgb(58, 57, 57);
}
.inputfieldcntr input{
    width: 100%;
    height: 50px;
    border: none;
    background-color: rgb(190, 190, 190);
    padding-left: 15px;
    font-family: robotolite;
    font-size: 20px;
    font-weight: 700;
    border-radius: 12px;
    color: #333;
}
.inputfieldcntr input:focus{
    outline: none;
    background-color: rgb(236, 213, 213);
}
.inputfieldcntr textarea{
    width: 100%;
    border: none;
    background-color: rgb(190, 190, 190);
    padding-top: 10px;
    padding-left: 15px;
    font-family: robotolite;
    font-size: 20px;
    font-weight: 700;
    border-radius: 12px;
    color: #333;
}
.inputfieldcntr textarea:focus{
    outline: none;
    background-color: rgb(236, 213, 213);
}
.inputfieldcntr button{
    width: 100%;
    height: 50px;
    border: none;
    font-family: robotolite;
    cursor: pointer;
    color: #ffffff;
    background-color: rgb(46, 168, 107);
    border-radius: 12px;
    font-size: 20px;
    transition: 0.3s linear;
}
.inputfieldcntr button:hover{
    background-color: rgb(20, 158, 89);
    color: #dadada;
}
.inputfieldcntr .socialmediabox{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 10px 5px;
}
.inputfieldcntr .socialmediabox svg{
    cursor: pointer;
    width: 30px;
}